@import "../main.scss";

.home-container {
  display: flex;
  height: 600px;
}

.home-background {
  width: 50%;
  height: 100%;
  background-image: url(../../assets/Home-Hero-Image.jpg);
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
}

.home-box {
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h1 {
    margin: 7px;
  }
}

.button-box {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
}



@media screen and (max-width: 990px) {
  .home-container {
    background-image: url(../../assets/Home-Hero-Image.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .home-background {
    display: none;
  }

  .home-box {
    width: 70%;
    height: 70%;
    background-color: #ffffffe0;
  }

  .main-button {
    width: 80%;
    height: 100%;
  }
}